import React, {useState, useEffect} from 'react';

import apis from './store/apis';
import json from './store/json.js';
import { observer } from 'mobx-react-lite';


const rounder = (number, mutch) => Math.round(number * mutch) / mutch;

function App () {
  const [tdr, setTdr] = useState([]); 
  const [erer, setErer] = useState([])
  let keyser = json.arrayrerKeys;
  
  useEffect(() => {
    const uri = 'https://api1.binance.com/api/v3';

    const getMas = async () => {
      const keyser = json.arrayrerKeys;
      Promise.all(
        keyser.map(keys => fetch(`${uri}/ticker/price?symbol=${keys}`).then(r=>r.json())) 
      ).then(ren => setTdr(ren));
    }

    getMas()
  }, [erer])

  let result = {spent: 0, actual: 0}

  // setInterval(setErer(erer + 1), 5000)

  let masser = tdr.map(({symbol, price}) => {
    // console.log(json.arrayrer[symbol]);
    let {amount, price: priceDt, howMoney, withdrawn} = json.funcInfo(json.arrayrer[symbol])
    
    if((amount < 1) || (symbol == "USDTUAH") || (symbol ==  "BTCUSDT" )){
      return
    }
    let mid = howMoney - withdrawn;
    let actual = amount * price;
    let buys = mid / amount;
    result.spent += mid;
    result.actual += actual;
    return (
      <li key={ symbol }>
        <p><strong> : { symbol }</strong>    { rounder(amount, 1) } </p>
        <p>    { rounder(buys, 1000) } ||    { rounder(priceDt, 1000) }</p>
        <p>   USDT { rounder(actual, 100) } ||   { rounder(price, 1000) }</p>
        <p>    { rounder(mid, 100) }</p>
        <p>   { rounder(actual - mid, 100) } ||  {rounder(( price / ( buys / 100 ) ), 1) - 100}%</p>
      </li>
    )
  })
  
  return (
    <div>
      <ul>{masser}</ul>
      <div>
        <p>: { rounder(result.spent, 10) } , : { rounder(result.actual, 10) } , : {rounder((result.spent - result.actual) * -1, 10)}  { rounder((result.actual / (result.spent / 100)) - 100, 10) }% </p>
      </div>
      <button type="button" className="btnUpdata" onClick={()=>{setErer(erer + 1)}}>UPDATA</button>
      
      <p>{keyser.join(', ')}</p>
    </div>
  )
}

export default observer(App)