import {makeObservable, action, observable, computed, runInAction} from 'mobx';

class ApisStore {
  link = null;


  async init(){
    let td1 = await fetch('https://api1.binance.com/api/v3/ticker/price?symbol=TWTUSDT');
    this.link = await td1.json();
  }

  constructor(){
    makeObservable(this, {
      link: observable,
      init: action.bound
    })
  }
}

export default new ApisStore();