import {makeObservable, action, observable, computed, runInAction} from 'mobx';


const averaging = (firstSoum,firstPrice,secondSoum,secondPrice) => ((firstSoum * firstPrice) + (secondSoum * secondPrice)) / (firstSoum + secondSoum);

const rounder = (number, mutch) => Math.round(number * mutch) / mutch;


function getElems(mass){
  let arrayrer = {};
  Object.keys(mass).map(key => {
    let timeElem = mass[key].Market;
    if(!arrayrer[timeElem]){
      arrayrer[timeElem] = [];
    }
    arrayrer[timeElem].push(mass[key])
  });;

  return arrayrer
}


class JsonStore {
  list = require('../../app/list7.json').reverse() ?? null;

  arrayrer = getElems(this.list);
  arrayrerKeys = Object.keys(this.arrayrer);

  funcInfo(arrayCoin) {
    let infoMyTrade = {
      amount: 0,
      price: 0,
      howMoney: 0,
      withdrawn: 0
    };
  
    arrayCoin.forEach(item=>{
      let amountItem = Number(item.Amount);
      let priceItem = Number(item.Price);
      let totalItem = amountItem * priceItem;
      if(item.Type == 'BUY'){
        infoMyTrade.price = averaging(infoMyTrade.amount,infoMyTrade.price,amountItem,priceItem);
        infoMyTrade.amount += amountItem;
        infoMyTrade.howMoney += totalItem;
      } else if (item.Type == 'SELL') {
        infoMyTrade.amount -= amountItem;
        infoMyTrade.withdrawn += totalItem;
        if(infoMyTrade.amount <= 0){
          infoMyTrade.amount = 0;
          infoMyTrade.price = 0;
          infoMyTrade.howMoney = 0;
          infoMyTrade.withdrawn = 0;
        }
      }
    })
  
    return infoMyTrade
  }
  

  
  

  constructor(){
    makeObservable(this, {
      list: observable
    })
  }
}

export default new JsonStore();